.index-section {
  @extend %container;
  margin-top: 4rem;
  h2 {
    margin-bottom: 1rem;
    font-family: $heading;
    font-weight: normal;
    text-transform: capitalize;
    color: lighten($link-color, 36%);
  }
}

.about {
  display: flex;
  align-items: center;
  @include responsive(480px) {
    position: relative;
    display: block;
  }
  .about-avatar {
    width: 48px;
    height: 48px;
    margin-right: 1rem;
    border-radius: 100%;
  }
  .about-desc {
    display: block;
    font-family: $sans;
    font-size: 80%;
  }
  .about-author {
    margin-right: 1rem;
    font-weight: 700;
    @include responsive(480px) {
      position: absolute;
      top: 20px;
      left: 58px;
    }
  }
  .about-social {
    @include responsive(480px) {
      position: absolute;
      top: -3px;
      left: 58px;
    }
  }
}

.article {
  li {
    margin-bottom: 1rem;
    @include responsive(480px) {
      margin: 0 0 1rem 0;
    }
    // a[rel] {
    //   margin-left: 1rem;
    //   @include responsive(480px) {
    //     margin-left: 0;
    //   }
    // }
    time {
      font-family: $sans;
      font-size: 90%;
      color: lighten($link-color, 22%);
      &:after {
        content: "—";
        margin: 0 1rem;
      }
      @include responsive(480px) {
        display: block;
        @include tailhook(.5rem);
      }
    }
  }
}

.more {
  padding-left: 13.5rem;
  > a {
    font-style: italic;
    color: #000;
  }
  @include responsive(480px) {
    padding-left: 0;
  }
}
